body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.weather {
    text-align: center;
}
.boxes {
    margin-top:30px;
    margin-left:300px;
}
.box {
    float:left;
    margin-left:20px;
    width:200px;
    text-align: center;
    height:220px;
}
.box:nth-child(1) {
    background-image: -webkit-linear-gradient( #b9c8e0, #6b85b6);
    background-image: linear-gradient( #b9c8e0, #6b85b6);
}
.box:nth-child(2) {
    background-image: -webkit-linear-gradient( #dededb, grey);
    background-image: linear-gradient( #dededb, grey);
}
.box:nth-child(3) {
    background-image: -webkit-linear-gradient( #fef0eb, #ade0f9);
    background-image: linear-gradient( #fef0eb, #ade0f9);
}
